  <br>  <br>
  <button v-on:click="showData">คลิกเพื่อดูข้อมูล</button>
  <button @click="showData">คลิกเพื่อดูข้อมูล</button>

  <h1>{{firstname}} {{lastname}}</h1>
  <h3>{{age}}</h3>
  <button @click="plus">เพิ่มอายุ</button>
  <button @click="red">ลดอายุ</button>
  <br> <br>
  <button @click.ctrl="plusa(5)">เพิ่มอายุ +5</button>
  <button @click="plusl(10)">ลดอายุ -10</button>
  <br> <br>
  <form>

    <label for="">ชื่อ : </label>
    <input type="text" v-on:input="sentname"><br><br>

    <label for="">นามสกุล : </label>
