    <input type="text" v-on:input="sentru"><br><br>

    <label for="">สาขา : </label>
    <input type="text" v-on:input="sentsaka"><br><br>

    <label for="">ห้อง : </label>
    <input type="text" v-on:input="sentroom"><br><br>

    <label for="">คติประจำใจ : </label>
    <input type="text" v-on:input="sentkati"><br><br>

    <label for="">เรียนต่อที่ไหน : </label>
    <input type="text" v-on:input="senttor"><br><br>

    <label for="">วิชาที่ไม่ชอบ: </label>
    <input type="text" v-on:input="sentnikname"><br><br>

  </form>

  <h2>ชื่อจริง : {{name}}</h2>
  <h2>นามสกุล : {{last}}</h2>
  <h2>ชื่อเล่น : {{nik}}</h2>
  <h2>อายุ : {{ru}}</h2>
  <h2>สาขา : {{saka}}</h2>
  <h2>ห้อง : {{room}}</h2>
  <h2>คติประจำใจ : {{kati}}</h2>
  <h2>เรียนต่อที่ไหน : {{tor}}</h2>
  <h2>วิชาที่ไม่ชอบ : {{vcha}}</h2>

</template>

<script>

export default {
  name: 'App',
  data(){
    return{
          firstname : "somphong",
          lastname : "doungjun",
          age : "20", 
          nikname : "",
          name : "",
          last : "",
          nik : "",
          ru : "",
          saka : "",
          room :"",
          kati : "",
          tor : "",
          vcha : ""
    }
  },
  methods:{
    showData(){
      alert(this.firstname + this.lastname)
    },
    plus(){
      this.age++
    },

    red(){
      this.age--
    },
    
    plusa(val){
      this.age+=val
    },

    plusl(val){
      this.age-=val
    },

    sentnikname(event){
      console.log(event.target.value);
      this.nikname = event.target.value
    },

    sentname(event){
      console.log(event.target.value);
      this.name = event.target.value
    },

    sentlast(event){
      console.log(event.target.value);
      this.last = event.target.value
    },

    sentnik(event){
      console.log(event.target.value);
      this.nik = event.target.value
    },

    sentru(event){
      console.log(event.target.value);
      this.ru = event.target.value
    },

    sentsaka(event){
      console.log(event.target.value);
      this.saka = event.target.value
    },

    sentroom(event){
      console.log(event.target.value);
      this.room = event.target.value
    },

    sentkati(event){
      console.log(event.target.value);
      this.kati = event.target.value
    },

     senttor(event){
      console.log(event.target.value);
      this.tor = event.target.value
    },

     sentvcha(event){
